import React, { useState } from 'react';

export interface ProductCardProps {
  icon: string;
  title: React.ReactNode;
  description: string;
  buttonText: string;
  onClick?: () => void;
}

export const ProductCard: React.FC<ProductCardProps> = ({
  icon,
  title,
  description,
  buttonText,
  onClick,
}) => {
  const [isActive, setIsActive] = useState(false);

  return (
    <div
      onMouseEnter={() => setIsActive(true)}
      onMouseLeave={() => setIsActive(false)}
      onClick={onClick}
      className={`
        w-[350px] rounded-4xl p-10 flex flex-col gap-10 cursor-pointer min-h-[563px]
        border-t-2
        transition-all duration-300 ease-in-out
        ${isActive
          ? 'bg-finsus-card-active border-t-transparent'
          : 'bg-gradient-to-b from-[#F6F6FB] to-white border-t-finsus-border'
        }
      `}
    >
      {/* Icon */}
      <div className="w-[140px] h-[140px] flex items-center justify-center">
        <img
          src={icon}
          alt=""
          className="w-full h-full object-contain"
        />
      </div>

      {/* Title */}
      <h3 className="text-xl font-semibold uppercase tracking-tight-custom font-poppins leading-[1.5] m-0">
        {isActive ? (
          <span className="text-finsus-dark">
            {typeof title === 'string' ? title : title}
          </span>
        ) : (
          title
        )}
      </h3>

      {/* Description */}
      <p className="text-base text-finsus-dark font-poppins leading-[1.5] m-0 flex-1">
        {description}
      </p>

      {/* Button */}
      <div className="pointer-events-none w-full">
        <div
          className={`
            flex items-center justify-start h-[60px] rounded-[20px]
            transition-all duration-300 ease-in-out relative overflow-hidden w-fit min-w-[200px]
            ${isActive
              ? 'bg-white p-1.5 pl-6 pr-20'
              : 'bg-finsus-dark p-1.5 pl-6 pr-6'
            }
          `}
        >
          <span
            className={`
              text-base font-poppins font-medium whitespace-nowrap block
              transition-all duration-300 ease-in-out
              ${isActive
                ? 'text-finsus-dark -translate-x-[5px] mr-[25px]'
                : 'text-white translate-x-0 mr-0'
              }
            `}
          >
            {buttonText}
          </span>
          <div
            className={`
              size-12 rounded-xl flex items-center justify-center flex-shrink-0
              transition-all duration-300 ease-in-out absolute right-1.5
              ${isActive
                ? 'bg-finsus-red opacity-100 translate-x-0'
                : 'bg-white opacity-0 translate-x-4'
              }
            `}
          >
            <svg
              width="20"
              height="20"
              viewBox="0 0 20 20"
              fill="none"
              className="-rotate-45"
            >
              <path
                d="M4 10H16M16 10L11 5M16 10L11 15"
                stroke={isActive ? '#FFFFFF' : '#23223F'}
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
