import React from 'react';

export interface CTAButtonProps {
  children: React.ReactNode;
  icon?: React.ReactNode;
  onClick?: () => void;
  className?: string;
  variant?: 'primary' | 'secondary' | 'white' | 'white-hover-red';
  textAlign?: 'left' | 'center' | 'right';
  hoverIcon?: React.ReactNode;
}

export const CTAButton: React.FC<CTAButtonProps> = ({
  children,
  icon,
  onClick,
  className = '',
  variant = 'primary',
  textAlign = 'center',
  hoverIcon,
}) => {
  const alignmentClasses = {
    left: 'justify-start',
    center: 'justify-center',
    right: 'justify-end'
  };

  const variantClasses = {
    primary: 'bg-finsus-dark text-white [@media(hover:hover)]:hover:bg-finsus-dark',
    secondary: 'bg-finsus-active text-white [@media(hover:hover)]:hover:bg-finsus-dark',
    white: 'bg-white text-finsus-dark',
    'white-hover-red': 'bg-white text-finsus-dark'
  };

  const arrowVariants = {
    primary: { bg: 'bg-white', stroke: '#23223f' },
    secondary: { bg: 'bg-white', stroke: '#23223f' },
    white: { bg: 'bg-finsus-dark', stroke: '#ffffff' },
    'white-hover-red': { bg: 'bg-white', stroke: '#FF4C3B' }
  };

  const arrow = arrowVariants[variant];

  return (
    <button
      onClick={onClick}
      className={`group px-6 py-4 rounded-[20px] flex items-center ${alignmentClasses[textAlign]} gap-3 transition-all duration-300 h-[64px] relative ${variantClasses[variant]} ${className} ${!icon ? '[@media(hover:hover)]:hover:pl-[68px]' : ''}`}
    >
      {/* Icono (si se proporciona) */}
      {icon && (
        <div className="transition-all duration-300 [@media(hover:hover)]:group-hover:opacity-0 [@media(hover:hover)]:group-hover:-translate-x-4">
          {icon}
        </div>
      )}

      {/* Texto */}
      <span className="font-medium text-base whitespace-nowrap transition-all duration-300 [@media(hover:hover)]:group-hover:-translate-x-[45px]">
        {children}
      </span>

      {/* Botón que aparece en hover */}
      <div className={`absolute right-2 top-1/2 -translate-y-1/2 rounded-[12px] size-[48px] ${arrow.bg} flex items-center justify-center opacity-0 translate-x-4 [@media(hover:hover)]:group-hover:opacity-100 [@media(hover:hover)]:group-hover:translate-x-0 transition-all duration-300 pointer-events-none`}>
        {hoverIcon || (
          <svg width="20" height="20" viewBox="0 0 20 20" fill="none" className="-rotate-45">
            <path d="M4 10H16M16 10L11 5M16 10L11 15" stroke={arrow.stroke} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        )}
      </div>
    </button>
  );
};

export default CTAButton;
