import React from 'react';

// Types for data that will come from backend
export interface FooterNavigationItem {
  label: string;
  href: string;
}

export interface FooterNavigationColumn {
  title: string;
  items: FooterNavigationItem[];
}

export interface FooterStoreButton {
  id: 'apple' | 'google';
  label: string;
  href: string;
}

export interface FooterContactEntry {
  id: string;
  label: string;
  href: string;
  iconType: 'phone' | 'phone-alt' | 'email' | null;
}

export interface FooterSocialIcon {
  id: 'facebook' | 'x' | 'instagram' | 'tiktok';
  href: string;
}

export interface FooterProps {
  /** Logo URL */
  logoUrl: string;
  /** Navigation columns data */
  navigationColumns: FooterNavigationColumn[];
  /** Store buttons (App Store, Google Play) */
  storeButtons: FooterStoreButton[];
  /** Contact entries (phone, email, etc) */
  contactEntries: FooterContactEntry[];
  /** Social media icons */
  socialIcons: FooterSocialIcon[];
  /** Contact card title */
  contactTitle?: string;
  /** Contact card subtitle */
  contactSubtitle?: string;
  /** Address text */
  address?: string;
  /** Legal text (without year) */
  legalText?: string;
  /** Additional className */
  className?: string;
}

