const contactIcons = {
  phone: PhoneIcon,
  'phone-alt': PhoneAltIcon,
  email: EmailIcon,
};

const socialIconComponents = {
  facebook: FacebookIcon,
  instagram: InstagramIcon,
  tiktok: TiktokIcon,
  x: XIcon,
};

export const Footer: React.FC<FooterProps> = ({
  logoUrl,
  navigationColumns,
  storeButtons,
  contactEntries,
  socialIcons,
  contactTitle = '¿Necesitas ayuda? Contáctanos',
  contactSubtitle = 'Lunes a viernes 24 horas. Sábados y domingos 8 a 20 horas',
  address = 'Av. Insurgentes Sur 1458 Piso 12, Col. Actipan, C.P 03230, Alcaldía Benito Juárez, CDMX',
  legalText = 'Finsus. Finanzas Transparentes.',
  className = '',
}) => {
  return (
    <footer className={`font-montserrat relative w-[95%] mx-auto mt-[60px] box-border pb-8 ${className}`}>
      <div className="flex flex-col gap-6">

        {/* Container 1: Navigation */}
        <div className="relative rounded-[32px] bg-finsus-blue-light-bg overflow-hidden p-8 md:p-10 lg:p-12">
          {/* Logo for mobile only */}
          <div className="w-full flex justify-start mb-8 lg:hidden">
            <img
              src={logoUrl}
              alt="Logo"
              className="w-[120px] h-[40px] md:w-[140px] md:h-[50px] object-contain"
            />
          </div>

          <div className="flex flex-col gap-8 md:gap-10 w-full">
            {/* First 4 columns */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-x-6 gap-y-8">
              {navigationColumns.slice(0, 4).map((column) => (
                <div key={column.title} className="flex flex-col gap-3">
                  <span className="text-base font-bold uppercase tracking-wide text-finsus-dark">
                    {column.title}
                  </span>
                  {column.items.map((item) => (
                    <a
                      key={`${column.title}-${item.label}`}
                      href={item.href}
                      className="text-base font-normal text-finsus-dark hover:text-finsus-active transition-colors duration-200 no-underline leading-relaxed"
                    >
                      {item.label}
                    </a>
                  ))}
                </div>
              ))}
            </div>

            {/* Remaining columns - Desktop Grid */}
            <div className="hidden lg:grid grid-cols-4 gap-x-6 gap-y-8">
              {navigationColumns.slice(4).map((column, index) => (
                <div key={`${column.title}-${index}`} className="flex flex-col gap-3">
                  <span className={`text-base font-bold uppercase tracking-wide ${column.title ? 'text-finsus-dark' : 'invisible'}`}>
                    {column.title || 'Spacer'}
                  </span>
                  {column.items.map((item) => (
                    <a
                      key={`${column.title}-${item.label}-${index}`}
                      href={item.href}
                      className="text-base font-normal text-finsus-dark hover:text-finsus-active transition-colors duration-200 no-underline leading-relaxed"
                    >
                      {item.label}
                    </a>
                  ))}
                </div>
              ))}
            </div>

            {/* Remaining columns - Mobile Flex */}
            <div className="flex flex-col gap-3 lg:hidden">
              {navigationColumns.slice(4).map((column, index) => (
                <React.Fragment key={`${column.title}-${index}`}>
                  {column.title && (
                    <span className="text-base font-bold uppercase tracking-wide text-finsus-dark mt-4 first:mt-0">
                      {column.title}
                    </span>
                  )}
                  {column.items.map((item) => (
                    <a
                      key={`${column.title}-${item.label}-${index}`}
                      href={item.href}
                      className="text-base font-normal text-finsus-dark hover:text-finsus-active transition-colors duration-200 no-underline leading-relaxed"
                    >
                      {item.label}
                    </a>
                  ))}
                </React.Fragment>
              ))}
            </div>
          </div>
        </div>

        {/* Container 2: Dark Theme Footer */}
        <div className="relative rounded-[32px] bg-finsus-dark overflow-hidden p-8 md:p-10 lg:p-12 text-white">

          {/* Top Section: Contact Card + Stores */}
          <div className="flex flex-col lg:flex-row justify-between gap-6 mb-8 md:mb-16">

            {/* Contact Card */}
            <div className="flex-1 bg-finsus-navy rounded-[24px] p-6 md:p-10 flex flex-col gap-5">
              <div>
                <h5 className="font-montserrat text-xl md:text-[37px] font-medium mb-2 text-white">
                  {contactTitle}
                </h5>
                <p className="font-montserrat text-[13px] md:text-lg m-0 text-finsus-gray">
                  {contactSubtitle}
                </p>
              </div>

              <div className="flex flex-wrap gap-3">
                {contactEntries.map((contact) => {
                  const IconComponent = contact.iconType ? contactIcons[contact.iconType] : null;

                  return (
                    <a
                      key={contact.id}
                      href={contact.href}
                      className="flex items-center gap-2.5 bg-finsus-navy-light rounded-[19px] px-6 py-4 md:px-[37px] no-underline hover:border-white/20 border border-transparent transition-all duration-200 group text-white"
                    >
                      {IconComponent && (
                        <span className="w-5 h-5">
                          <IconComponent />
                        </span>
                      )}
                      <span className="font-montserrat text-lg md:text-[21px] font-bold text-white group-hover:opacity-90">
                        {contact.label}
                      </span>
                    </a>
                  );
                })}
              </div>

              <address className="not-italic font-montserrat text-xs md:text-lg leading-relaxed text-finsus-gray mt-2">
                {address}
              </address>
            </div>

            {/* Store Buttons */}
            <div className="flex flex-col gap-4 justify-start min-w-full lg:min-w-[240px]">
              {storeButtons.map((button) => {
                const isApple = button.id === 'apple';

                return (
                  <a
                    key={button.id}
                    href={button.href}
                    className="flex items-center gap-3 rounded-[28px] border border-white bg-transparent hover:bg-white/10 px-6 py-6 w-full lg:w-[240px] transition-all duration-200 no-underline box-border text-white"
                  >
                    <span className="shrink-0">
                      {isApple ? <AppleIcon /> : <GooglePlayIcon />}
                    </span>
                    <span className="font-montserrat text-[25px] font-semibold text-white leading-none">
                      {button.label}
                    </span>
                  </a>
                );
              })}
            </div>
          </div>

          {/* Divider */}
          <div className="h-px w-full bg-white/10 mb-8" />

          {/* Bottom Section */}
          <div className="flex flex-col gap-6">
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-6">
              {/* Logo */}
              <img
                src={logoUrl}
                alt="Logo"
                className="w-[120px] h-[40px] md:w-[160px] md:h-[60px] object-contain brightness-0 invert"
              />

              {/* Social Icons */}
              <div className="flex gap-4">
                {socialIcons.map((social) => {
                  const IconComponent = socialIconComponents[social.id];
                  return (
                    <a
                      key={social.id}
                      href={social.href}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="flex items-center justify-center w-10 h-10 rounded-full hover:scale-110 transition-transform duration-200"
                    >
                      <IconComponent />
                    </a>
                  );
                })}
              </div>
            </div>

            {/* Legal Text */}
            <div className="flex flex-col lg:flex-row justify-between items-start lg:items-center gap-3 text-finsus-gray text-xs md:text-[13px] font-montserrat leading-relaxed">
              <span>
                {legalText} {new Date().getFullYear()} | www.finsus.mx |
                <a
                  href="https://finsus.mx/tablas-de-costos-y-comisiones"
                  className="ml-1 text-finsus-gray hover:text-white transition-colors duration-200 no-underline"
                >
                  Consulta los costos y comisiones de nuestros productos
                </a>
              </span>
            </div>
          </div>

        </div>
      </div>
    </footer>
  );
};

export default Footer;
